<template>
  <div class="user-wrap">
    <van-nav-bar
      title="收券"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    />
    <van-field
      readonly
      clickable
      label="第一类别"
      :value="type1"
      placeholder="选择第一类别"
      @click="showPicker1 = true"
    />
    <van-field
      readonly
      clickable
      label="第二类别"
      :value="type2"
      placeholder="选择第二类别"
      @click="showPicker2 = true"
    />
    <van-field
      readonly
      clickable
      label="第三类别"
      :value="type3"
      placeholder="选择第三类别"
      @click="showPicker3 = true"
    />
    <van-field 
      label="转让单价"
      v-model="price" 
      disabled 
    />
    <van-notice-bar :scrollable="false">
      当前可寄售 {{canSell}} 张
    </van-notice-bar>
    <van-tabs v-model="active">
      <van-tab title="单张寄售">
        <div class="quan-contain">
          <van-cell-group>
            <van-field v-model="quanma" placeholder="请输入券码" />
          </van-cell-group>
        </div>
      </van-tab>
      <van-tab title="批量寄售">
        <van-field name="uploader" label="上传附件">
          <template #input>
            <van-uploader v-model="uploader" />
          </template>
        </van-field>
      </van-tab>
    </van-tabs>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="客服" @click="goPage('/qscode')" />
      <van-goods-action-icon icon="star-o" text="收藏" />
      <van-goods-action-button type="danger" text="立即寄售" color="#07c160" @click="onSubmit"/>
    </van-goods-action>
    <van-popup v-model="showPicker1" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns1"
        @cancel="showPicker1 = false"
        @confirm="onConfirm1"
      />
    </van-popup>
    <van-popup v-model="showPicker2" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns2"
        @cancel="showPicker2 = false"
        @confirm="onConfirm2"
      />
    </van-popup>
    <van-popup v-model="showPicker3" position="bottom">
      <van-picker
        show-toolbar
        :columns="columns3"
        @cancel="showPicker3 = false"
        @confirm="onConfirm3"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: 'parchase',
  components: {
  },
  data () {
    return {
      type1: '',
      type2: '',
      type3: '',
      price: '¥2131',
      canSell: 32,
      active: 0,
      showPicker1: false,
      showPicker2: false,
      showPicker3: false,
      columns1: ['1a', '1b', '1c', '1d', '1e'],
      columns2: ['2a', '2b', '2c', '2d', '2e'],
      columns3: ['3a', '3b', '3c', '3d', '3e'],
      quanma: '',
      uploader: []
    }
  },
  computed: {
  },
  mounted () {
  },
  methods: {
    onConfirm1(value) {
      this.type1 = value
      this.showPicker1 = false
    },
    onConfirm2(value) {
      this.type2 = value
      this.showPicker2 = false
    },
    onConfirm3(value) {
      this.type3 = value
      this.showPicker3 = false
    },
    onClickLeft () {
      this.$router.go(-1)
    },
    onSubmit () {
      console.log('submit')
    },
    goPage (url, type) {
      this.$router.push({ path: url, query: {type: type} })
    }
  }
}
</script>

<style scoped lang="less">
  .quan-contain {
    padding: 10px 0;
  }
</style>


